import React from 'react'
import Image from 'next/image'
import Link from 'next/link'

const BlogCard = ({ blog }) => {
  return (
    <Link href={blog.slug} className='flex last:mb-0'>
      <div className='shadow-[0px_0px_10px_0px_#0000000D] overflow-hidden flex flex-col bg-white border-[#EEEEEE] w-full rounded-[12px] border border-solid lg:rounded-2xl hover:shadow-lg transition-shadow duration-300'>
        {/* Thumbnail */}
        <div className='shrink-0 relative bg-[#d1d1d1] group aspect-w-16 aspect-h-9'>
          <div className='relative w-full pt-[56.25%]'>
            <Image
              src={blog.image_url}
              alt={blog.title}
              fill
              className='object-cover'
              sizes='(max-width: 768px) 50vw, (max-width: 1024px) 33vw, 25vw'
            />
          </div>

          {/* Play Button Overlay */}
          <div className='absolute inset-0 flex items-center justify-center'>
            <div className='w-12 h-12 lg:w-16 lg:h-16'>
              <svg
                width='100%'
                height='100%'
                viewBox='0 0 20 20'
                fill='none'
                xmlns='http://www.w3.org/2000/svg'
                className='drop-shadow-lg'
              >
                <circle cx='10' cy='10' r='9.5' stroke='#FAFAFA' />
                <path
                  d='M6.45834 5.3474V14.2363C6.45832 14.3351 6.48466 14.4322 6.53467 14.5175C6.58467 14.6027 6.65652 14.6731 6.7428 14.7214C6.82908 14.7696 6.92667 14.7939 7.0255 14.7919C7.12433 14.7898 7.22082 14.7614 7.30501 14.7096L14.5272 10.2652C14.6081 10.2155 14.6749 10.1459 14.7213 10.063C14.7676 9.98014 14.7919 9.88678 14.7919 9.79184C14.7919 9.6969 14.7676 9.60355 14.7213 9.52068C14.6749 9.43782 14.6081 9.36822 14.5272 9.31851L7.30501 4.87407C7.22082 4.82227 7.12433 4.79387 7.0255 4.79181C6.92667 4.78974 6.82908 4.81409 6.7428 4.86232C6.65652 4.91056 6.58467 4.98094 6.53467 5.06621C6.48466 5.15148 6.45832 5.24855 6.45834 5.3474Z'
                  fill='#FAFAFA'
                />
              </svg>
            </div>
          </div>
        </div>

        {/* Content */}
        <div className='flex justify-center flex-col rtl:pr-2.5 rtl:pl-6 lg:px-4 lg:pt-[10px] bg-white px-[4px] py-[8px]'>
          {/* Category */}
          <div className='block items-center font-medium text-[11px] text-[#5C2D91] lg:text-[12px] rtl:text-[12px] lg:rtl:text-[14px] leading-[120%] rtl:leading-[140%] mb-1 lg:mb-[10px] truncate'>
            {blog.category}
          </div>

          {/* Title */}
          <div className='line-clamp-2 text-[12px] rtl:text-[14px] lg:text-[16px] font-medium text-[#1A1A1A] break-words leading-[120%] rtl:leading-[140%]'>
            {blog.title}
          </div>
        </div>
      </div>
    </Link>
  )
}


export default BlogCard;